.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;

  margin-block-end: 10px;
  writing-mode: horizontal-tb;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;

  height: 100%;
  width: 100%;
  position: absolute;
}
