.top {
  width: 10%;
  height: 10%;
  background-color: white;
  position: sticky;
  top: 0;
   font-size: 11px;
  z-index: 999;
  font-family: "Josefin Sans", sans-serif ,15px;
  
}



.topLeft{
  flex: 3;
  display: flex;
}


.btn{
  margin-left: 0;
      display: flex;
 
}

@media only screen and (min-width: 600px) {
  .top.btn{ 
    margin-left: 55%;
    display: flex;
    width: 10%;
  }
  
} 

@media only screen and (min-width: 1000px) {
  .top.btn{ 
    margin-left: 75%;
    display: flex;
    width: 10%;
  }
  
} 


@media only screen and (min-width: 1600px) {
  .top.btn{ 
    margin-left: 85%;
    display: flex;
    width: 10%;
  }
  
} 


.topIcon {
  font-size: 20px;
  margin-right: 10px;
  color: #444;
  cursor: pointer;
}



.topList {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px;
  padding: 0;
  list-style: none;
}

.topListItem {
  margin-right: 20px;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
}

.topListItem:hover{
    color: gray;
}

.topImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 15px;
  cursor: pointer;
}

.topSearchIcon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
}

