.header {
  width: auto;
  justify-content: center;
 
  
}


.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
   margin-bottom: 50px;
}

.headerTitleSm {
  display: flex;
  top: 30%;
  font-size: 30px;
}

.headerTitleLg {
  display: flex;
  top: 22%;
  font-size: 20px;
}

.headerImg {
  display: flex;
  width: 100%;
  height: 450px;
  object-fit: cover;

}
.img{
   mask-image: linear-gradient(rgba(0, 0, 0, 1.0), transparent);
 
 
  }
  
/*
*
* ==========================================
* CUSTOM UTIL CLASSES
* ==========================================
*
*/
.megamenu {
  position: static;
}

.megamenu .dropdown-menu {
  background: none;
  border: none;
  width: 100%;
}

